@import url(https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,100;0,300;0,400;0,700;0,900;1,100;1,300;1,400;1,700;1,900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
body{margin:0;font-family:"Roboto",sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro,Menlo,Monaco,Consolas,"Courier New",monospace}.cat-panel{width:24vw;height:100vh;position:fixed;background:#fafafa}.bar-chart{display:block}.bar{transition:height .2s cubic-bezier(0.32, 1.13, 0.67, 1.75);display:block;margin:10px}.bar:hover{background:orange}.bar-and-text{display:inline-block;font-size:14px;font-weight:500}.cat-display{background:#f0f0f0;position:absolute;top:0;left:24vw;z-index:-1}.cat-display .flip-move{display:flex;flex-wrap:wrap}.cat-card{height:180px;width:180px;margin:45px;font-size:20px;font-weight:300;align-items:center}.cat-card .caption{margin-top:7px}.cat-card img{display:block;transition:width .2s cubic-bezier(0.32, 1.13, 0.67, 1.75),height .2s cubic-bezier(0.32, 1.13, 0.67, 1.75),transform .2s cubic-bezier(0.32, 1.13, 0.67, 1.75);height:180px;width:180px;margin:auto}.cat-card img:hover{height:200px;width:200px}.popup{min-width:100%;min-height:100%;background:#fff}.popup img{display:inline-block;float:left;margin-left:60px;margin-top:30px;width:20vw;height:20vw}.popup .cat-info{float:left;width:30vw;margin-left:100px;text-align:left;display:inline-block;padding-top:40px}.popup .cat-info .breed-name{font-size:60px;font-weight:300;margin-bottom:30px}.popup .cat-info .facts-title,.popup .cat-info .alt-name{font-size:20px;font-weight:300;font-style:italic;margin:0}.popup .cat-info .metadata{font-size:20px;font-weight:600;margin:5px 0}.popup .cat-info .description{font-size:20px;font-weight:400;margin-top:30px}.popup .cat-info .description div{margin:10px 0}.popup .cat-info .history{font-size:20px;font-weight:300;margin-top:30px}.popup .cat-info .history div{margin:10px 0}.all-cats .aPPpx{position:fixed;left:24vw;top:0;display:flex;background:#f0f0f0;z-index:1}.all-cats .aPPpx .GWiDP{border-radius:2px;min-width:calc(100% - 130px);min-height:calc(100% - 130px);display:flex}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

